.welcome {  
  top: -62px;
  padding-top: 111px;

  background-image: url(__static__content/welcome-bg.png);
  background-repeat: no-repeat;
  background-position: 0;
  background-size: cover; 

    @media (max-width: $third) {
      padding-top: 100px;
      height: 480px;
      overflow: hidden;

      background-position: 80% 0;
    }

    @media (max-width: $fourth) {
      height: 400px;
    }

    .wrap {
      display: flex;
      justify-content: space-between;
      padding-right: 32px;
    }

    &_content {
      max-width: 560px;
      margin-top: 24+18px;
      width: 100%;
      
        @media (max-width: $first) {
          max-width: 437px;
        }

        @media (max-width: $second) {
          z-index: 10;
          max-width: 381px;
        }

        @media (max-width: $third) {
          margin-top: 0;
        }
    }

      &_h2 {
        margin-bottom: 26px;

          @media (max-width: $second) {
            margin-bottom: 15px;
          }
      }

      &_desc {
        max-width: 520px;
        width: 100%;

        color: #301d28;
        line-height: 23.8px;
        font-size: 18px;

          @media (max-width: $first) {
            max-width: 380px;

            font-size: 14px;
            line-height: 18.51px;
          }

          @media (max-width: $fourth) {
            max-width: 284px;

            font-size: 12px;
            line-height: 15.2px;
          }

          p {
            margin: 0;
          }

          b {
            font-weight: 600;
          }
      }

      &_button {
        margin-top: 37px;

          @media (max-width: $first) {
            margin-top: 30px;
          }

          @media (max-width: $second) {
            margin-top: 25px;
          }

          @media (max-width: $fourth) {
            margin-top: 17px;
          }
      }

    &_imgs {
      max-width: 597px;
      width: 100%;
      height: 530px;

        @media (max-width: $first) {
          max-width: 396px;
          height: 352px;
        }

        @media (max-width: $second) {
          position: absolute;
          right: -20px;

          max-width: 303px;
          height: 309px;
        }

        @media (max-width: $third) {
          max-width: 300px;
          height: 205px;
        }

        @media (max-width: $fourth) {
          max-width: 192px;
          height: 143px;
        }
    }

      &_img {
        max-width: 100%;

          @media (max-width: $second) {
            overflow: hidden;
          }

          @media (max-width: $third) {
          }

          img {
            display: block;
            max-width: 100%;
          }

          &._ball {
            position: absolute; 
            top: 18px;  
            left: -2px;

              @media (max-width: $first) {
                width: 48+10px;
                height: 49+10px;

                  img {
                    object-fit: cover;
                    width: 100%;
                  }
              }

              @media (max-width: $second) {
                left: 22px;

                width: 6+39px;
                height: 6+39px;
              }

              @media (max-width: $third) {
                bottom: -53px;
                top: auto;
                left: -25px;

                height: 31+10px;
                width: 31+10px;
              }

              @media (max-width: $fourth) {
                bottom: -55px;
                left: -80+30px;

                height: 18+7px;
                width: 18+7px;
              }
          }

          &._doggo {

              @media (max-width: $second) {
                position: absolute;
                top: 40px;

                max-width: none;
                width: 299+30px;
                height: 255+30px;
              }

              @media (max-width: $third) {
                top: 100px;
                right: -10px;

                height: 183+100px;
                width: 180+100px;
              }

              @media (max-width: $fourth) {
                top: 120px;
                right: 10px;

                height: 183+20px;
                width: 180+20px;
              }
          }
      }
}
