.header {
  z-index: 10;
  
  padding-top: 21px;
  padding-bottom: 20px;

  box-shadow: 0 18px 7px rgba(0, 0, 0, 0.05);
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  background-color: #ffffff;

    @media (max-width: $first) {
      padding-top: 18px;
      padding-bottom: 18px;
    }

    @media (max-width: $second) {
      padding-top: 15px;
      padding-bottom: 16px;
    }

    @media (max-width: $fourth) {
      padding-top: 11px;
      padding-bottom: 12px;
    }

    .wrap {
      display: flex;
      justify-content: space-between;
      align-items: center;

        @media (max-width: $second) {
          justify-content: center;
        }
    }

    &_logo {

    }

    &_menu {
      margin-top: 1px;
    }
}
