.banner {
  top: -6px;

  max-width: 100%;
  max-height: 992px;

    @media (max-width: $second) {
      overflow: hidden;
    }

    &_img {
      display: block;
      width: 100%;
      max-height: 100%;
      object-fit: cover;

        @media (max-width: $second) {
          width: 130%;
          margin-left: -13%;
        }

        @media (max-width: $third) {
          margin-left: 0;
          width: 100%;
        }

        &._desc {

            @media (max-width: $third) {
              display: none;
            }
        }
        &._phone {
          display: none;

            @media (max-width: $third) {
              display: block;
              width: 220%;
              margin-left: -65%;
            }
        }
    }
}
