.feat {
  display: flex;
  align-items: center;
  width: 30%;
  margin-bottom: 67px;

    @media (max-width: $second) {
      width: 50%;

        &:nth-child(even) {
          padding-left: 55px;
        }
    }

    @media (max-width: $second) {
      margin-bottom: 28px;
      flex-direction: column;
      justify-content: center;
      text-align: center;
    }

    @media (max-width: $fourth) {
      justify-content: flex-start;
      margin-bottom: 12px;

        &:nth-child(even) {
          padding-left: 24px;
        }
    }

    &:last-child {
      width: 100%;
      margin-bottom: 0;

        &:after {
          content: '';

          position: absolute;
          right: 28px;
          bottom: 43px;

          display: block;
          width: 192px;
          height: 14px;

          background-image: url(__static__content/feat-last-line.png);
          background-repeat: no-repeat;
          background-position: 50% 50%;

             @media (max-width: $first) {
              bottom: 35px;
              right: 80px;
             }

              @media (max-width: $second) {
                right: 0;
                display: none;
              }

              @media (max-width: $third) {
                display: none;
              }
        }

        @media (max-width: $second) {

            .feat_title {
              max-width: 343px;
            }
        }
    }

    &_img-container {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 104px;
      height: 104px;
      margin-right: 34px;

      border-radius: 50%;
      background-color: rgba(211, 245, 253, 0.46);

         @media (max-width: $first) {
          width: 82px;
          height: 82px;
          margin-right: 17px;
         }

         @media (max-width: $third) {
          margin-right: 0;
          margin-bottom: 16px;
         }

         @media (max-width: $fourth) {
          width: 53px;
          height: 53px;
          margin-bottom: 13px;
         }

        img {
          display: block;

             @media (max-width: $first) {
              max-width: 42px;
              max-height: 42px;
             }

             @media (max-width: $fourth) {
              max-width: 28px;
              max-height: 28px;
             }
        }
    }

      &_counter {
        z-index: 10;
        position: absolute;
        left: 0;
        top: 0;

        display: flex;
        align-items: center;
        justify-content: center;
        width: 31px;
        height: 31px;

        font-size: 19px;
        font-weight: 700;
        color: #ffffff;

        border-radius: 50%;
        box-shadow: 0 3px 7px rgba(255, 163, 201, 0.92);
        background: linear-gradient(131.25deg, #FF5497 -0.48%, #D21858 112.69%);

           @media (max-width: $first) {
            width: 25px;
            height: 25px;

            font-size: 16px;
           }

           @media (max-width: $fourth) {
            width: 16px;
            height: 16px;

            font-size: 12px;
           }
      }

      img {
        display: block;
      }

    &_title {
      font-size: 24px;
      font-weight: 600;
      color: #45383d;

         @media (max-width: $first) {
          font-size: 18px;
         }

         @media (max-width: $third) {
          font-size: 16px;
         }

         @media (max-width: $fourth) {
          font-size: 12px;
         }
    }
}
