.join-item {
  display: flex;
  justify-content: space-between;
  max-width: 699px;
  width: 100%;
  height: 232px;
  padding-right: 38px;

  box-shadow: 0 17px 21px rgba(0, 165, 217, 0.2);
  border-radius: 20px;
  border: 5px solid #00a5d9;
  background-color: #ffffff;
  margin-right: 10px;

    &:last-child {
      margin-right: 0;
    }

     @media (max-width: $first) {
      max-width: 497px;
      height: 276px;
      padding-right: 27px;
     }

     @media (max-width: $second) {
      margin-bottom: 41px;
      margin-right: 0;

        &:last-child {
          margin-bottom: 0;
        }

        &:first-child {

            .join-item_desc {
              max-width: 297px;
            }
        }
     }

     @media (max-width: $third) {
      border: 2px solid #00a5d9;
     }

     @media (max-width: $fourth) {
      height: 100%;
     }

    &_img {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 173px;
      height: 100%;

      border-top-left-radius: 10px;
      border-bottom-left-radius: 10px;
      background-color: rgba(211, 245, 253, 0.48);

         @media (max-width: $first) {
          max-width: 123px;
         }

         @media (max-width: $third) {
          max-width: 99px;
         }

         @media (max-width: $fourth) {
          max-width: 70px;
          height: auto;
         }

        img {
          display: block;

            @media (max-width: $first) {
              max-width: 59px;
            }

            @media (max-width: $third) {
              max-width: 47px;
            }

           @media (max-width: $fourth) {
            max-width: 34px;
           }
        }
    }

    &_content {
      display: flex;
      flex-direction: column;
      max-width: 446px;
      padding-top: 31px;
      width: 100%;
      padding-bottom: 32px;

         @media (max-width: $first) {
          max-width: 320px;
          padding-top: 22px;
          padding-bottom: 0;
         }

         @media (max-width: $third) {
          margin-left: 23px;
         }

         @media (max-width: $fourth) {
          padding-bottom: 19px;
         }
    }

      &_desc {
        width: 100%;
        height: 93px;
        display: flex;
        align-items: center;
        margin-bottom: 17px;

        font-weight: 500;
        line-height: 22px;
        color: #301d28;
        font-size: 18px;

           @media (max-width: $first) {
            height: 130px;
            margin-bottom: 46px;
           }

           @media (max-width: $third) {
            font-size: 14px;
            line-height: 20px;
           }

           @media (max-width: $fourth) {
            margin-bottom: 13px;
            height: auto;

            font-size: 12px;
            line-height: 16px;
           }

          p {
            margin: 0;
          }
      }

      &_button {
        max-width: 223px;

           @media (max-width: $first) {
            max-width: 219px;
            font-size: 18px;
           }

           @media (max-width: $second) {
            height: 50px;
           }

           @media (max-width: $third) {
            height: 40px;
            max-width: 169px;
            padding-left: 20px;

            font-size: 14px;

              &:after {
                right: 16px;

                width: 14px;
                height: 11px;
                margin-top: -6.5px;

                background-size: 14px 11px;
              }
           }

           @media (max-width: $fourth) {
            height: 29px;
            max-width: 118px;
            padding-left: 13px;

            font-size: 10px;

              &:after {
                right: 12px;

                width: 9px;
                height: 7px;
                margin-top: -3.5px;

                background-size: 9px 7px;
              }
           }
      }
}
