.feats {
  max-width: 1310px;

     @media (max-width: $first) {
      max-width: 1017px;
     }

     @media (max-width: $second) {
      max-width: 644px;
      margin-top: 25px;
     }

     @media (max-width: $third) {
      margin-top: -60px;
     }

    &_h2 {
      text-align: center;

        @media (max-width: $third) {
          font-size: 20px;
        }
    }

    &_content {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      margin-top: 40px;

        @media (max-width: $second) {
          margin-top: 26px;
        }
    }
}
