// Common styles of the project

* {
  position: relative;
  box-sizing: border-box;
}

html, body {
  height: 100%;
  margin: 0;
  padding: 0;

  font-family: $Gilroy;
}

.button {
  display: flex;
  align-items: center;
  padding-left: 28px;
  padding-bottom: 3px;
  max-width: 206px;
  height: 52px;
  width: 100%;

  color: #f6f6f6;
  text-align: center;
  text-decoration: none;
  font-weight: 700;
  font-size: 18px;
  line-height: 23.3px;

  box-shadow: 0 7px 15px rgba(255, 163, 201, 0.92);
  border-radius: 26px;
  background: linear-gradient(131.25deg, #FF5497 -0.48%, #D21858 112.69%);

    @media (max-width: $first) {
      max-width: 175px;
      height: 47px;
      padding-bottom: 2px;

      line-height: 19.42px;
      font-size: 15px;
    }

    @media (max-width: $fourth) {
      height: 36px;
      max-width: 132px;
      padding-left: 13px;

      line-height: 15.53px;
      font-size: 12px;
    }

    &:after {
      content: '';

      position: absolute;
      right: 25px;
      top: 50%;

      margin-top: -6px;
      width: 16px;
      height: 12px;

      background-image: url(__static__content/arrow-right.png);
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-size: 16px 12px;

      transition: 0.3s; 

        @media (max-width: $first) {
          width: 14px;
          right: 21px;

          background-size: 14px 12px;
        }

        @media (max-width: $fourth) {
          right: 15px;

          width: 12px;
          height: 10px;
          margin-top: -5px;

          background-size: 12px 10px;
        }
    }

    &:hover {

          &:after {
            transform: translateX(2px);
          }
    }
}

.wrap {
  max-width: 1474-30px;
  width: 100%;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;

    @media (max-width: $first) {
      max-width: 1015-30px;
    } 

    @media (max-width: $second) {
      max-width: 644-30px;
    }

    @media (max-width: $third) {
      max-width: none;
      padding-left: 16px;
      padding-right: 16px;
    }

    @media (max-width: $fourth) {
      padding-left: 17px;
      padding-right: 17px;
    }
}

.logo {
  display: block;
  max-width: 243px;
  width: 100%;
  height: 45px;

    @media (max-width: $first) {
      max-width: 191px;
      height: 35px;
    }

    @media (max-width: $second) {
      max-width: 213px;
      height: 39px;
    }

    @media (max-width: $fourth) {
      max-width: 145px;
      height: 29px;
    }

    img {
      display: block;
      width: 100%;
    }
}

.menu {

  @media (max-width: $second) {
    display: none;
  }

    &_item {
      margin-right: 37px;

      font-weight: 700;
      font-size: 18px;
      color: #45383d;
      text-decoration: none;

        @media (max-width: $first) {
          margin-right: 27px;

          font-size: 16px;
        }

        &:last-child {
          margin-right: 0;
        }

        &:after {
          content: '';

          position: absolute;
          bottom: -3px;
          left: -2px;
          right: -4px;

          height: 3px;

          background-color: #ff5497;
          opacity: 0;
          border-radius: 10px;
          transition: 0.3s;

            @media (max-width: $first) {
              bottom: -7px;
              right: 1px;
              left: 0;
            }
        }

        &._active {

            &:after {
              opacity: 1;
            }
        }
    }
}

.h2 {
  margin: 0;

  font-size: 34px;
  font-weight: 800;
  color: #45383d;

    @media (max-width: $first) {
      font-size: 24px;
    }

    @media (max-width: $second) {
      font-size: 22px;
    }

    @media (max-width: $fourth) {
      font-size: 17px;
    }
}