.join {
  top: -23px;

  max-width: 1438px;

     @media (max-width: $first) {
      max-width: 1024px;
     }

    &_h2 {
      text-align: center;

        @media (max-width: $second) {
          font-size: 24px;
        }
    }

    &_content {
      margin-top: 46px;
      display: flex;
      justify-content: space-between;

        @media (max-width: $second) {
          margin-top: 35px;
          flex-direction: column;
          align-items: center;
        }
    }
}
