.contacts {
  margin-top: 118px;
  display: flex;
  justify-content: space-between;
  padding-right: 27px;

    @media (max-width: $second) {
      margin-top: 38px;
    }

    @media (max-width: $third) {
      margin-top: 19+5px;
    }

    @media (max-width: $fourth) {
      margin-top: 7px;
    }

    @media (max-width: $fourth) {
      padding-right: 17;
    }

    &_content {
    }

      &_header {
        display: flex;
        align-items: center;
      }

        &_img {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 104px;
          height: 104px;

          border-radius: 50%;
          background-color: rgba(211, 245, 253, 0.46);

            @media (max-width: $first) {
              width: 82px;
              height: 82px;
            }

            @media (max-width: $fourth) {
              width: 66px;
              height: 66px;
            }

            img {
              display: block;

                @media (max-width: $first) {
                  max-width: 38px;
                }

                @media (max-width: $fourth) {
                  max-width: 31px;
                }
            }
        }

        &_h2 {
          margin-top: 4px;
          margin-left: 29px;

            @media (max-width: $first) {
              margin-left: 24px;
              margin-top: 1px;
            }

            @media (max-width: $second) {
              font-size: 24px;
            }

            @media (max-width: $fourth) {
              font-size: 15px;
              margin-left: 8px;
            }
        }

      &_desc {
        max-width: 706px;
        width: 100%;
        margin-top: 33px;

        font-size: 20px;
        font-weight: 500;
        color: #301d28;
        line-height: 27px;

          @media (max-width: $first) {
            max-width: 560px;

            font-size: 18px;
          }

          @media (max-width: $third) {
            max-width: none;

            font-size: 14px;
          }

          @media (max-width: $fourth) {
            margin-top: 13px;
            
            font-size: 11px;
          }

          p {
            margin: 0;
            margin-bottom: 20px;

              @media (max-width: $third) {
                margin-bottom: 8px;
              }

              @media (max-width: $fourth) {
                margin-bottom: 0;
              }

              &:last-child {
                margin-bottom: 0;
              }
          }

          b {

          }
      }

        &_unique {
          display: inline-block;

          margin-bottom: 27px;
          font-weight: 700;
          padding-top: 13-4px;
          padding-bottom: 13-4px;
          padding-left: 24-2px;
          padding-right: 23-2px;

          line-height: 22px;

          border-radius: 29px;
          border: 3px solid #ea387a;

            @media (max-width: $third) {
              display: inline-flex;
              align-items: center;
              height: 38px;
              margin-right: 9px;
              margin-bottom: 8px;

                &:last-child {
                  margin-right: 0;
                }
            }

            @media (max-width: $fourth) {
              height: 32px;
              margin-right: 8px;
              padding-left: 13px;
              padding-right: 14px;
            }
        }

      &_button {
        max-width: 304px;
        padding-left: 36px;
        margin-top: 43px;
        height: 58px;

          @media (max-width: $first) {
            margin-top: 25px;
            font-size: 18px;
          }

          @media (max-width: $third) {
            height: 40px;
            max-width: 219px;
            padding-left: 21px;

            font-size: 14px;
          }

          @media (max-width: $fourth) {
            height: 37px;
            max-width: 198px;

            font-size: 12px;
          }

          &:after {
            right: 34px;

              @media (max-width: $first) {
                right: 34px;

                width: 16px;
                height: 12px;

                background-size: 16px 12px;
              }

              @media (max-width: $third) {
                right: 17px;

                width: 14px;
                height: 11px;

                background-size: 14px 11px;
              }

              @media (max-width: $fourth) {
                right: 19px;

                width: 13px;
                height: 10px;

                background-size: 13px 10px;
              }
          }
      }

    &_doggo {
      margin-top: 78px;

        @media (max-width: $first) {
          position: absolute;
          right: -40px;
          top: 20px;

          max-width: 530px;
          height: 344px;
        }

        @media (max-width: $second) {
          top: auto;
          bottom: -120-300+20px;

          left: 50%;

          max-width: 570px;
          height: 380px;
          margin-left: -285px;
        }

        @media (max-width: $third) {
          left: 0;
          right: 0;
          bottom: -450px;

          max-width: 428px;
          width: 100%;
          margin-left: 0;
          margin: 0 auto;
        }

        @media (max-width: $fourth) {
          bottom: -343px;
          
          height: 272px;
        }

        img {
          width: 100%;
        }
    }
}
