.footer {
  margin-top: 141+300px;

  box-shadow: 0 -18px 7px rgba(0, 0, 0, 0.05);
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;

    @media (max-width: $third) {
      margin-top: 450-50px;
    }

    @media (max-width: $fourth) {
      margin-top: 317px;
    }

    &_container {      
      padding-top: 21px;
      padding-bottom: 20px;

      background-color: #ffffff;
      border-top-left-radius: 30px;
      border-top-right-radius: 30px;

        @media (max-width: $second) {
          // padding-top: 300px;
        }

        @media (max-width: $third) {
          padding-top: 8px;
          padding-bottom: 8px;
        }

        &:before {
          content: '';

          z-index: -1;
          position: absolute;
          top: -141-300+20px;
          left: 0;
          right: 0;
          bottom: 0;

          background-image: url(__static__content/footer-img.png);
          background-repeat: no-repeat;
          background-position: 50% -86px;
          background-size: cover;

            @media (max-width: $first) {
              background-position: 50% -60px;
            }

            @media (max-width: $second) {
              background-size: 1286px 664px;
            }

            @media (max-width: $third) {
              top: -400px;
            }

            @media (max-width: $fourth) {
              top: -309px;
            }
        }
    }

    .wrap {
      display: flex;
      justify-content: space-between;
      align-items: center;

        @media (max-width: $second) {
          justify-content: center;
        }
    }

    &_logo {

        @media (max-width: $third) {
          max-width: 201px;
          height: 37px;
        }
    }

    &_menu {
      margin-top: 1px;
    }
}
